import React from "react"
import styled from "styled-components";
import StyledSocialIcons from "./StyledSocialIcons";

const Footer = styled.div`
    position: fixed;
    left: 0;
    overflow: hidden;
    bottom: 0;
    width: 100%;
    color:white;
    padding: 1%;
    margin: 0;
    color:${props => props.textColor};
    p{
        padding: 0 50px 0 0 ;
        margin: 0; 
        bottom: 0;
        font-size: 16px;
        color:${props => props.textColor_p};
        float: right;
    }
    @media (max-width: 1024px){
       position:static;
       background-color:#191919;
       text-align: center;
       p{
           float:none;
           padding:1% 0% 5% 0%;
           font-size:11px;
       }
    }
`



export const StyledFooter = (props) => {
    // const [style, setStyle] = useState({display: 'none'});

    return(
            <Footer class="footer-main">
                {/* <a href="#"  className="social"><i class="social-icons fab fa-linkedin-in fa-2x" style={{color:`${props.textColor}`}}></i> LinkedIn </a> */}
                <StyledSocialIcons classes="fab fa-linkedin-in fa-x" textColor={props.textColor} name="linkedIn"/>
                <StyledSocialIcons classes="fab fa-twitter fa-x" textColor={props.textColor} name="twitter"/>
                <StyledSocialIcons classes="fas fa-envelope fa-x" textColor={props.textColor} name="email"/>
                {/* <br /> */}
                <p className="footer-p"  style={{color:`${props.textColor_p}`}}> 2021 Â© Avirup Debnath</p>
            </Footer>
    )
}