import React from 'react';
import { StyledTile } from '../StyledComponents/StyledTile';

export const  Main = (props) => {
    return (
            <div className="tile-container">
                <StyledTile name="About Me" background="/portfolio/images/about-me.jpg" link="/portfolio/about-me"/>
                <StyledTile name="Experience" background="/portfolio/images/experience.jpg" link="/portfolio/experience"/>
                <StyledTile name="Projects" background="/portfolio/images/project.png" link="/portfolio/projects"/>
                <StyledTile name="Skills" background="/portfolio/images/skills.jpg" link="/portfolio/skills"/>
             </div> 
       
    );
}
