import { useState } from "react";
import { A } from "hookrouter";
import StyledCircularButton from "../StyledComponents/StyledCircularBtn";
import styled, { withTheme } from "styled-components";

const ButtonHolder = styled.div``;

const Tile = styled.div`
    float:left;
    margin: 0;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-image: url(${props => props.backgroundImage});
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 25%;

    A{
        margin: 0;
        padding: 0;
        h2{
            color:white;
        }
        &:hover{
            text-decoration: none;
            h2{
                color:#ffd369;
            }
            ${ButtonHolder}{
                display:block;
            }
        }
    }
   
    ${ButtonHolder}{
        display:none;
    }    


    @media (max-width: 1024px){
        float:none;
        margin: 0;
        text-decoration:none;
        overflow: hidden;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        background-image: url(${props => props.fixedImage});
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
        max-height: -webkit-fill-available;
        width: 100%;
        
        ${ButtonHolder}{
                display:block;
        }
}`


export const StyledTile = (props) => {
    const imgs = ['',`${props.background}`];
    let [bcgImg,setBcgImg]=useState(imgs[0]);

    return(
        <A href={props.link} tabindex='-1' style={{color:"white", textDecoration:"none"}}>
        <Tile backgroundImage={bcgImg} fixedImage={props.background}>
                    <A href={props.link} onMouseEnter={ () => setBcgImg(imgs[1]) } onMouseLeave={ () => setBcgImg(imgs[0])} >
                    <h2 className="tile-heading" >{props.name}</h2>
                    <ButtonHolder> 
                        <StyledCircularButton color="black" marginTop="150px"/>
                    </ButtonHolder>
                </A>
        </Tile>
        </A>
    )
}
