import React, { useEffect, useState } from "react";
import styled from "styled-components";

const StyledIcon = styled.a`
    color: white;
    padding: 0% 1% ;

    i{
        font-size: 16px;
        padding: 0.5% 1%;
        margin: 0; 
    }
    span{
            display:none;
            font-size: 16px;
            padding: 0;
            margin: 0;
    }

    &:hover{
        color:white;
        text-decoration:none;
        span{
            display:inline;
        }
    }


@media (max-width: 1024px){
    i {
        padding: 5% 7% 5% 7%;
    }
    &:hover span{
            display:none;
    }
}
`

function StyledSocialIcons(props) {
    let states = ['none', ''];
    const [style, setStyle] = useState(states[0]);
    return (
            <StyledIcon href="#">
            <i class= {`${props.classes}`} style={{color:`${props.textColor}`}}></i> 
                    <span > 
                        {props.name} 
                    </span> 
            </StyledIcon>
    );
}

export default StyledSocialIcons;